.details{
    width:100%;
    height:100vh;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    padding-top: 85px;
}
.wrap{
    width:100%;
    height:100%;
    background-color: rgba(0,0,0,0.5);
}
.bottom-wrap{
    width:100%;
    height:45%;
    position:absolute;
    bottom:0;
}
.bottom-details{
    width:90%;
    height:100%;
    background-color: rgba(255,255,255);
    margin: 0 auto;
    border-radius: 15px 15px 0 0;
    padding-top:25px;
}
.top-details-wrap{
    width:100%;
    height:30%;
    position:absolute;
    bottom:45%;
}
.top-details h1, .top-details h4{
    color: #fff;
    font-family: 'Montserrat', sans-serif;
}
.top-details h4{
    margin-top:0;
    font-size:18px;
}
.top-details{
    width:90%;
    margin:0 auto;
    height: 100%;
}
.donate{
    width:250px;
    height: 45px;
    background-color: rgba(255,255,255);
    border-radius: 10px;
}
.d-btn,.s-btn{
    width:50%;
    height:80%;
    float:left;
    margin-top: 4.5px;
}
.d-btn{
    border-right: 1.3px solid #000;    
}
.b-details-head{
    width:85%;
    margin:0 auto;
    height:30%;
}
.b-details-head h1, .b-details-head h4{
    color: #000;
    margin:0;
    font-family: 'Montserrat', sans-serif;
}
.b-details-head h4{
    font-size: 16px;
    letter-spacing: 1px;
}
.s-btn img,.d-btn img{
    height:25px;
    margin: 5.5px 0 0 10px;
    float: left;
}
.s-btn p, .d-btn p{
    float: left;
    font-family: 'Dongle', sans-serif;
    line-height:1.3;
    font-size :30px;
    margin-left: 10px;
}
.s-btn,.d-btn:hover{
    cursor: pointer;
}
.head-left{
    width:50%;
    float: left;
}
.head-right{
    width:50%;
    height:100%;
    float: left;
    display:flex;
    justify-content: end;
    align-items: center;
}
.contact{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border: 1px solid #000;
    border-radius:10px;
    padding:10px;
}
.contact img{
    width: 25px;
    float: left;
}
.contact h5{
    margin: 0 10px 0 35px;
    line-height:1.5;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
}
.contact:hover{
    cursor: pointer;
}
.b-details-wrap{
    width:85%;
    height:100px;
    margin:50px auto;
}
.b-details{
    width:25%;
    float: left;
    height:100%;
}
.b-details img{
    height:45px;
    float: left;
}
.b-details h5,.b-details h6{
    line-height:2;
    font-family: 'Montserrat', sans-serif;
}
.b-details h6{
    margin-left: 40px;;
}
.b-time img{
    padding:5px;
}

@media only screen and (max-width:768px){
    .bottom-details{
        width:100%;
    }
    .details{
        height:98vh;
    }
    .b-details{
        width:90%;
    }
}
